.schedule-container h3 {
    text-align: center;
    margin-top: 20px;

}

.aerrow-back2 {
    font-size: 25px;
    background-color: white;
    border: none;
    outline: none;
    position: absolute;
    top: 52px;
    left: 15px;
}

.date-container {
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
    align-items: center;
}

.date-container .date-counts {

    padding: 3px 12px 20px 12px;
    border-radius: 5px;

}

.date-container .date-counts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;

}

.date-counts:hover {
    background: linear-gradient(90.95deg, #DEE5FF 0.79%, #809AFF 99.18%);
    color: #fff;
}

.date-counts .date {
    font-size: 12px;
    font-weight: 500;

}

.schedule-container h5 {
    text-align: center;
    margin-top: 10px;
}

.time-table {
    position: relative;
}

.time-table p {
    margin: 8px 0px 8px 0px;
    margin-left: 10px;
    font-size: 12px;
    font-weight: 600;

}

.adworkout {
    width: 150px;
    background: linear-gradient(270deg, #D3A4F4 0%, #E9B1E0 102.35%);
    text-align: center;
    padding: 0.5px 1px;
    border-radius: 20px;
    color: #fff;
    position: absolute;

    top: 40px;
    right: 10px;

}


.adworkout2 {

    width: 200px;
    background: linear-gradient(270deg, #D3A4F4 0%, #E9B1E0 102.35%);
    text-align: center;
    padding: 0.5px 1px;
    border-radius: 20px;
    color: #fff;
    position: absolute;
    top: 90px;
    right: 50px;

}

.line-container {
    z-index: 2;
    width: 250px;
    height: 2px;
    background-color: blue;
    position: absolute;
    top: 120px;
    right: 50px;
}

.dot-container {
    width: 7px;
    height: 7px;
    background-color: blue;
    border-radius: 50%;
    position: absolute;
    top: 118px;
    left: 34px;
}

.lowebody-container{
    background-color: #beb7b7;
    padding: 1px;
    width: 180px;
    text-align: center;
    border-radius: 20px;
    position: absolute;
    bottom: 130px;
    right: 70px;
}
.plus-icon{
    width: 50px;
    height: 50px;
    background: linear-gradient(90.95deg, #E9B1E0 0.79%, #D3A5F2 99.18%);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    
    position: absolute;
    bottom: 60px;
    right: 30px;
    
}
.plus-icon a{
    color: #fff;
    font-weight: 600;
}

.tracker-footer2 {

    z-index: 2;
    background-color: #fff;
    width: 100%;
    height: 60px;
    position: absolute;
    top: 90%;
    border-radius: 0px 0px 10px 10px;
}

.tracker-footer2{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tracker-footer2 .content2 {
    font-size: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    padding: 5px 20px;
    margin-top: 5px;

}

.content2 a {
    color: #5d5757;
}

.content2 a:hover {
    color: #1f3fc1;
}

.search-icon2 {
    position: absolute;
    z-index: 4;
    top: 88%;
    left: 42%;
    width: 50px;
    height: 50px;
    background: linear-gradient(90.95deg, #DEE5FF 0.79%, #809AFF 99.18%);
    font-size: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    border-radius: 50%;
}