.Tracker-container {
    position: relative;
}


.Tracker-container h3 {
    text-align: center;
    margin-top: 20px;
}

.chart-container {
    width: 300px;
    margin-left: 10px;
    margin-top: 10px;


}

.good {
    position: absolute;
    font-size: 9px;
    font-weight: 400;
    top: 70px;
    left: 73px;

}

.less {
    position: absolute;
    font-size: 9px;
    font-weight: 400;
    top: 70px;
    left: 130px;
}

.waring {
    margin-top: 10px;
    margin-left: 10px;
    background: linear-gradient(90.07deg, rgba(129, 157, 255, 0.28) 5.94%, rgba(16, 16, 16, 0) 76.45%);
    display: flex;
    align-items: center;

    padding: 10px;
    border-radius: 10px;
    gap: 10px;
}

.waring .waring-icon {
    width: 20px;
    height: 20px;
    color: blue;
}

.waring p {
    font-size: 12px;
    font-weight: 500;
    width: 250px;
}

.upcoming {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 15px;
    margin-top: 15px;
}

.upcoming p {
    font-size: 12px;
    color: #938888;
    font-weight: 500;
}

.worket-image1 img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.worket-image2 img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.workouts-area {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 10px;
    padding: 5px 15px;



}

.workout1-area {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.303);
    border-radius: 10px;
    padding: 3px 5px;


}

.worket-image1 {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.workout2-area {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    padding: 3px 5px;
}

.worket-image2 {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;

}

.images-detalis {
    display: flex;
    flex-direction: column;
    gap: 5px;


}

.toggle {
    font-size: 30px;
    color: blue;
    margin-right: 10px;
}

.images-detalis h5 {
    font-size: 12px;
    font-weight: 600;
}

.images-detalis p {
    font-size: 9px;
}

.aerrow-back {
    font-size: 25px;
    background-color: white;
    border: none;
    outline: none;
    position: absolute;
    top: 3px;
    left: 15px;

}

.train {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #8CB1FF99;
    border-radius: 10px;
    padding: 0px 10px;

}
.chart-container{
    text-align: center;
}

.train img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.train-details {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.train-details p {
    font-size: 12px;
}

.train-details h5 {
    font-weight: 500;
}

.tracker-footer {

    z-index: 2;
    background-color: #fff;
    width: 100%;
    height: 60px;
    position: absolute;
    top: 92%;
    border-radius: 0px 0px 10px 10px;
}

.tracker-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tracker-footer .content {
    font-size: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    padding: 5px 20px;
    margin-top: 5px;

}

.content a {
    color: #5d5757;
}

.content a:hover {
    color: #1f3fc1;
}

.search-icon {
    position: absolute;
    z-index: 4;
    top: 88%;
    left: 42%;
    width: 50px;
    height: 50px;
    background: linear-gradient(90.95deg, #DEE5FF 0.79%, #809AFF 99.18%);
    font-size: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    border-radius: 50%;
}