
.container h4{
    margin-top: 30px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 20px;
    font-weight: 600;
    margin-left: 20px;
    
}

.input-container{
     margin-top: 30px;
     display: flex;
     flex-direction: column;
     gap: 25px;
     padding: 10px 15px ;

}
.input-container input{
    padding: 12px 15px;
    border-radius: 10px;
    outline: none;
    border: none;
    background-color:#F1F1F1;
    font-weight: 500;
    
}

.checkbox-container{
    margin-top: 10px;
    gap: 8px;
    display: flex;
    margin-left: 20px;  
}
.checkbox-container input{
    width: 18px;
    height: 18px;
    
}
.checkbox-container p{
    margin-top: 3px;
    font-size: 10px;
    color:#b5a7a7 ;
    font-weight: 500;
}
.checkbox-container span{
    color:#3553ca;
    margin:0px 5px;
    text-decoration: underline;
}

.account-container{
   text-align: center;
   margin-top: 60px;
}
.account-container button{
    padding: 10px 70px;
    border-radius: 12px;
    font-weight: 600;
    color: #fff;
    outline: none;
    border: none;
    background: linear-gradient(270deg, #7B91FF 0%, #95BEFF 100%);


}
.line{
    text-align: center;
    margin-top: 14px;
    font-size: 12px;
    font-weight: 600;
}
.line::before{
    content: "";
    background-color: #c8b6b6;
    width: 100px;
    height: 0.1px;
    display: block;
    position: absolute;
    top: 82%;
    left: 55px;
} 
.line::after{
    content: "";
    background-color: #c8bebe;
    width: 100px;
    height: 0.1px;
    display: block;
    position: absolute;
    top: 82%;
    right: 55px;
} 

.logo{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    margin-top: 15px;
}
 
.box{
    padding:8px  8px;
    border: 1.5px #847979 solid;
    border-radius: 5px;
    display: flex;

}
.login{

    text-align: center;
    margin-top: 18px;
    font-size: 14px;
    font-weight: 500;
}
.PASSWORD{
    width: 100%;
}







