.goals-container h3{
    text-align: center;
    margin-top: 30px;
}

.box2{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 13px 15px;
    background-color:#F1F1F1 ;
    border-radius: 10px;
}

.input-container3{
    display: flex;
    flex-direction: column;
    padding: 20px 20px;
    gap: 20px;

}
.box2 input{
    width: 15px;
    height: 15px;
}
.box2 p{

    font-size: 12px;
    font-weight: 600;

}
.confirm{
    text-align: center;
    margin-top: 70px;
}
.confirm button{
     padding: 10px 120px;
     border: none;
     border-radius: 10px;
     outline: none;
     background: linear-gradient(270deg, #7B91FF 0%, #95BEFF 100%);
     color: #fff;
     font-weight: 600;
}