.login-container h4{
    margin-top: 30px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 20px;
    font-weight: 600;
    margin-left: 20px;
    
}
.input-container2{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 25px;
    padding: 10px 15px ;
    position: relative;
    
}

.input-container2 input{
    padding: 12px 15px;
    border-radius: 10px;
    outline: none;
    border: none;
    background-color:#F1F1F1;
    font-weight: 500;
    
}
.login-container p{
    margin-left: 20px;
    font-size: 13px;
    color:#766f6f ;
    font-weight: 500;
    text-decoration: underline;
}
.eye{
   position: absolute;
   top: 200px;
   right: 30px;
   color: #766f6f;
   font-size: 20px;
}
.account-container2 button{
    padding: 10px 70px;
    border-radius: 12px;
    font-weight: 600;
    color: #fff;
    outline: none;
    border: none;
    background: linear-gradient(270deg, #7B91FF 0%, #95BEFF 100%);
    

}
.account-container2{
    text-align: center;
    margin-top: 205px;
 }

.login-container .line2{
    text-align: center;
    margin-top: 11px;
    font-size: 12px;
    font-weight: 600;
    text-decoration: none;
    margin-right: 20px;
    color: #000;
}
.line2::before{
    content: "";
    background-color: #c8b6b6;
    width: 100px;
    height: 0.1px;
    display: block;
    position: absolute;
    top: 82%;
    left: 55px;
} 
.line2::after{
    content: "";
    background-color: #c8bebe;
    width: 100px;
    height: 0.1px;
    display: block;
    position: absolute;
    top: 82%;
    right: 55px;
} 

.logo2{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    margin-top: 15px;
}
 
.box{
    padding:8px  8px;
    border: 1.5px #847979 solid;
    border-radius: 5px;
    display: flex;

}

.account2{

    text-align: center;
    margin-top: 18px;
    font-size: 14px;
    font-weight: 500;
    margin-right: 13px;
}


























